import React from "react";
import AboutImg from "../assets/images/about-img.jpeg";
const About = () => {
    return ( 
    <>
        <section className = "about"
        id = "about" >
        <h1 className = "heading" >
        <span > about </span> us </h1>

        <div className = "row" >
        <div className = "image" >
        <img src = { AboutImg }
        alt = "" />
        </div>

        <div className = "content" >
        <h3 > what makes our Chai special ? </h3> <p >
        
        </p> <p >
        Our different flavours of chai makes you feel fresh.
        </p> <a href = "#"
        className = "btn" >
        learn more </a> </div> </div> </section> 
        </>
    );
};

export default About;